<template>
  <div class=" m-auto relative">
    <div
        class="border-2 border-black p-3 shadow-cartoon fixed bottom-0 text-black left-0 right-0 max-w-md m-auto my-2 bg-yellow-400 font-bold">
      <template v-for="(item,index) in items" :key="item.name">
        <router-link :to="item.path">{{ item.name }}</router-link>
        <span v-if="index < count-1"> | </span>
      </template>
      <!--      |-->
      <!--      <router-link to="/favorite">Favorite</router-link>-->
      <!--      |-->
      <!--      <router-link to="/skill">Skill</router-link>-->
      <!--      |-->
      <!--      <router-link to="/recommend">Recomend me</router-link>-->
      <!--      |-->
      <!--      <router-link to="/about">About me</router-link>-->
    </div>
  </div>
</template>
<script>

export default {
  name: "Navigation",
  components: {},
  created() {
    this.$router.options.routes.forEach(route => {
      this.count++
      this.items.push({
        name: route.name
        , path: route.path
      })
    })
  }
  , data() {
    return {
      items: [],
      count: 0,
    }
  }
}
</script>

